___CSS_LOADER_EXPORT___.push([module.id, `:root {
	/* Color */
	--primary-color: #1a1a1a;
	--secondary-color: #ff5733;

	--red-orange: #ff5733;
	--white: #ffffff;
	--dark-blue: #0070c0;
	--light-red: #ffc0cb;
	--gold: #ffd700;

	--display-secondary: #ffffff;
	--subtext: #cccccc;

	/* Font Family */
	--font-display: "Playfair Display", serif;
	--font-subtext: "Montserrat", sans-serif;

	/* Font Size */
	--base: 16px;
	--p-sm1: 14px;
	--p-sm2: 12px;

	--h1: 36px;
	--h2: 24px;
	--h3: 18px;
}

a {
	text-decoration: none;
	color: var(--white);
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: var(--font-subtext);
	font-size: var(--base);
	color: var(--white);
	background-color: var(--primary-color);
    position: relative;
}

.skip-link-content{
    text-decoration: none;
    color: white;
    background-color: orange;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 180%);
    border-radius: 10px;
    padding: 6px;
    font-size: 2rem;
    padding: 5px 20px;
    margin-top: 1.5rem;
    min-height: 44px;
    display: flex;
    align-items: center;
}

.skip-link-content a{
    color: white;
}

.skip-link-content:focus{
    color: orange;
    background-color: white;
    transform: translate(-50%, 750%);
}

.app-bar{
    padding: 8px 16px;
    background-color: white;
    display: grid;
    grid-template-columns: auto 1fr auto;
    gap: 10px;
    position: sticky;
    top: 0;
    z-index: 99;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar__menu  {
    display: flex;
    align-items: center;
}

.app-bar__brand {
    display: flex;
    align-items: center;
}

.app-bar .app-bar__menu button{
    background-color: transparent;
    border: none;
    font-size: 18px;
    padding: 8px;
    cursor: pointer;
    display: inline-block;
    min-width: 44px;
    min-height: 44px;
}
.app-bar .app-bar__brand a {
    color: black;
    text-transform: uppercase;
    font-size: 20px;
    user-select: none;
    position: relative;
}

.app-bar .app-bar__brand a span{
    color: orange;
    position: absolute;
    top: 100%;
    left: 0;
}

.app-bar .app-bar__navigation {
    position: absolute;
    top: 50px;
    left: -180px;
    width: 150px;
    transition: all 0.3s;
    padding: 8px;
    background-color: white;
    overflow: hidden;
}

.app-bar .app-bar__navigation.open {
    left: 0;
}

.app-bar .app-bar__navigation ul li a {
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: black;
    padding: 15px;
    margin-bottom: 5px;
    width: 100%;
    min-width: 44px;
    min-height: 44px;
}

/* content */

.hero-element{
    position: relative;
}

.hero-element img{
    width: 100%;
    height: auto;
    display: block;
    transition: all 0.5s ease-out;
}

.hero-element h2{
    color: orange;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -220%);
    font-size: 20px;
}

.hero-element p{
    font-size: 8px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -20%);
    color: white;
    text-align: center;
}

.skip-link {
    position: absolute;
    top: -40px;
    left: 0;
    background-color: orange;
    color: white;
    padding: 8px;
    z-index: 150;
}

.skip-link:focus {
    top: 0;
}

.restorant-list{
    width: 100%;
    align-content: center;
    padding: 3rem;
    min-width:44px;
    min-height:44px;
}

.restorant-list img{
    width: 200px;
    transition: transform 0.3s;
}

.restorant-list h2{
    text-align: center;
    color: orange;
}
.restorant-list {
    position: relative;
    justify-content: center; /* Mengatur ke tengah secara horizontal */
    align-items: center; /* Mengatur ke tengah secara vertikal */
    border: 1px solid black;
    border-radius: 5px;
    text-align: center;
    margin: 10px 0;
}
.restorant-list .content{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%,-3%);
    background: rgba(0, 0, 0, 0.6);
    text-align: center;
    padding: 5px 0;
    width: 100%;
    height: 25%;
    
}

.card {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	background-color: #000000;
	border-radius: 16px;
	transition: all 300ms ease;
	overflow: hidden;
    max-height: 575px;
}

.card:hover {
	transform: scale(1.03);
}
.card:hover .card-image > .city {
	background-color: var(--gold);
	color: var(--primary-color);
}

.card > .card-image {
	display: flex;
	flex-direction: column;
	position: relative;
	max-height: 280px;
	overflow: hidden;
	cursor: pointer;
	transition: all 300ms ease;
	filter: grayscale(100%);
}

.card:hover > .card-image {
	filter: unset;
}

.card > .card-image > .city {
	position: absolute;
	top: 18px;
	left: -1px;
	padding: 10px 24px;
	background-color: var(--primary-color);
	border-radius: 0 4px 4px 0;
	font-family: var(--font-subtext);
	color: var(--display-secondary);
	font-weight: 500;
	text-transform: uppercase;
	transition: all 300ms ease;
}

.card > .card-image > img {
	width: 100%;
	vertical-align: middle;
	object-fit: cover;
	transition: all 300ms ease;
    height: 300px;
}

.card > .card-body {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	gap: 8px;
	padding: 16px;
    width: 600px;
}

.card > .card-body > .subtext {
	font-family: var(--font-subtext);
	font-size: var(--h3);
	color: var(--white);
	font-weight: 600;
}
.card > .card-body > .subtext > #rating {
	color: var(--gold) !important;
}

.card > .card-body > .display {
	font-family: var(--font-subtext);
	font-size: var(--h2);
	color: var(--gold);
	font-weight: 700;
	text-transform: uppercase;
}

.card > .card-body > .desc {
	font-family: var(--font-subtext);
	font-size: var(--base);
	color: var(--white);
	font-weight: 400;
	text-align: justify;
}


.content h3{
    color: white;
}

.content h3 a{
    color: white;
    display: inline-block;
    min-width: 44px;
    min-height: 44px;
}

.restorant-list div img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.nama-restorant{
    text-align: center;
}

.ratting{
    text-align: center;
    font-weight: bold;
}

.card-detail{
    border: 1px solid black;
    width: 300px;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    flex-direction: column;
}

.card-detail h2{
    text-align: center;
    margin: 1rem 0;
}

.restaurant-info{
    margin: 2rem;
    text-align: justify;
}

i.fas.fa-star {
    text-align: center;
    color: gold;
}

.menu-section {
    margin-top: 20px;
}

.menu-section h2 {
    text-align: left;
    font-size: 20px;
    font-weight: bold;
}

.menu-list {
    
    padding-left: 15px;
}

.menu-item {
    margin-top: 10px;
}

.restaurant-categories {
    margin-top: 20px;
}

.restaurant-info p{
    margin-top: 20px;
}

.customer-reviews h2{
    text-align: left;
}

.like {
    width: 55px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #db0000;
    
    position: fixed;
    bottom: 16px;
    right: 16px;
    border-radius: 50%;
    border: 0;
    
    font-size: 18px;
    color: white;
    cursor: pointer;
}

footer{
    width: 100%;
    background-color: orange;
    text-align: center;
    margin: 5rem 0 5rem 0;
    padding: 0.5rem;
}

footer a{
    display: inline-block;
    min-width: 44px;
    min-height: 44px;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;CACC,UAAU;CACV,wBAAwB;CACxB,0BAA0B;;CAE1B,qBAAqB;CACrB,gBAAgB;CAChB,oBAAoB;CACpB,oBAAoB;CACpB,eAAe;;CAEf,4BAA4B;CAC5B,kBAAkB;;CAElB,gBAAgB;CAChB,yCAAyC;CACzC,wCAAwC;;CAExC,cAAc;CACd,YAAY;CACZ,aAAa;CACb,aAAa;;CAEb,UAAU;CACV,UAAU;CACV,UAAU;AACX;;AAEA;CACC,qBAAqB;CACrB,mBAAmB;AACpB;;AAEA;CACC,sBAAsB;CACtB,SAAS;CACT,UAAU;AACX;;AAEA;CACC,gCAAgC;CAChC,sBAAsB;CACtB,mBAAmB;CACnB,sCAAsC;IACnC,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;IACrB,YAAY;IACZ,wBAAwB;IACxB,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,mBAAmB;IACnB,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,gCAAgC;AACpC;;AAEA;IACI,iBAAiB;IACjB,uBAAuB;IACvB,aAAa;IACb,oCAAoC;IACpC,SAAS;IACT,gBAAgB;IAChB,MAAM;IACN,WAAW;IACX,0CAA0C;AAC9C;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,6BAA6B;IAC7B,YAAY;IACZ,eAAe;IACf,YAAY;IACZ,eAAe;IACf,qBAAqB;IACrB,eAAe;IACf,gBAAgB;AACpB;AACA;IACI,YAAY;IACZ,yBAAyB;IACzB,eAAe;IACf,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,SAAS;IACT,OAAO;AACX;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,YAAY;IACZ,YAAY;IACZ,oBAAoB;IACpB,YAAY;IACZ,uBAAuB;IACvB,gBAAgB;AACpB;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,qBAAqB;IACrB,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,WAAW;IACX,eAAe;IACf,gBAAgB;AACpB;;AAEA,YAAY;;AAEZ;IACI,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,cAAc;IACd,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,iCAAiC;IACjC,eAAe;AACnB;;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,OAAO;IACP,wBAAwB;IACxB,YAAY;IACZ,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,MAAM;AACV;;AAEA;IACI,WAAW;IACX,qBAAqB;IACrB,aAAa;IACb,cAAc;IACd,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,0BAA0B;AAC9B;;AAEA;IACI,kBAAkB;IAClB,aAAa;AACjB;AACA;IACI,kBAAkB;IAClB,uBAAuB,EAAE,yCAAyC;IAClE,mBAAmB,EAAE,uCAAuC;IAC5D,uBAAuB;IACvB,kBAAk